import React from 'react';
import NombreUsuario from './NombreUsuario';
import Avatar from './Avatar';
import imagen from './imagenes/bulma.png';

const Header = () => {

    const usuario = {
        nombre:"Lara",
        apellido:"Molina"
    }

    return (
        <header className="Header">
            <h2>ComIT</h2>
            <NombreUsuario user={usuario}/>
            <Avatar img={imagen}/>
        </header>
    );
};

export default Header;