import React,{useState} from 'react';

const Formulario = (props) => {

    const [dato,setDatos] = useState({
        id:null,
        nombre:'',
        oficio:''
    });

    function handleChange(e){
        setDatos({
            ...dato,
            [e.target.name]:e.target.value
        })
    }

    function submitForm(e) {
        e.preventDefault()

        props.cambio(dato)
        setDatos({
            id:null,
            nombre:'',
            oficio:''
        })
    }

    return (
        <form className="Formulario" onSubmit={submitForm}>
            <label>Nombre:</label>
            <input type="text" value={dato.nombre} name="nombre" onChange={handleChange} required/>
            <label>Oficio:</label>
            <input type="text" value={dato.oficio} name="oficio" onChange={handleChange} required/>
            <input type="submit" className="boton"/>
        </form>
    );
};

export default Formulario;