import React from 'react';

function Deletebutton(props) {

    return(
        <div className="delete">
            <button onClick={()=>
                props.delete(props.usuario.id)
            }>
                Delete
            </button>
        </div>
    );
}

const Tablehead = () => {
    return (
        <thead className="Table">
            <tr>
                <th>Name</th>
                <th>Job</th>
                <th>Remove</th>
            </tr>
        </thead>
    );
};

function Tablebody(props) {
    console.log(props.datoImput)
    return(<tbody>
        {   
            
            props.datoImput.map((user) => (
            <tr key={user.id}> 
                <td>{user.nombre}</td>
                <td>{user.oficio}</td>
                <td><Deletebutton delete={props.delete} usuario={user}/></td>
            </tr>
        ))
        
    }
    </tbody>);
}

const Table = (props) => {
    return (
            <table>
              <Tablehead/>
              <Tablebody datoImput={props.datosInput} delete={props.deleteUser}/>
            </table>
        
    );
};

export default Table;