import React,{useState,useEffect}from 'react';

const Clock = () => {
    
    const [hora,setHora] = useState(new Date());

    useEffect(()=>{
        function tick() {
            setHora(new Date());
        }

        setInterval(()=>tick(),1000);
    });


    return (
        <div className="reloj">
            {hora.toLocaleTimeString()}
        </div>
    );
};

export default Clock;